"use strict";

var React = React;
var ctx = ctx;
var messages = lski.messages;
var contentsStore = contentsStore;
var contentsActions = contentsActions;

var BrowserDetailsBar = React.createClass({
	displayName: "BrowserDetailsBar",
	_getState: function _getState() {
		return {
			selectedItem: contentsStore.getSelected()
		};
	},
	getInitialState: function getInitialState() {
		return this._getState();
	},
	componentDidMount: function componentDidMount() {
		contentsStore.addListener(this._update);
	},
	componentWillUnmount: function componentWillUnmount() {
		contentsStore.removeListener(this._update);
	},
	_update: function _update() {
		this.setState(this._getState());
	},
	onDeleteClicked: function onDeleteClicked() {
		var _this = this;

		messages.confirm('Are you sure you want to delete this file? \n\nIt could be being used', function (answer) {

			if (!answer) {
				return;
			}

			contentsActions.deleteItem(_this.state.selectedItem.id);
		});
	},
	render: function render() {

		var item = this.state.selectedItem;

		if (!item || !item.isFile) {
			return null;
		}

		return React.createElement(
			"footer",
			{ className: "details" },
			React.createElement(
				"div",
				null,
				React.createElement("img", { className: "delete", src: "content/images/close.png", onClick: this.onDeleteClicked }),
				React.createElement(
					"a",
					{ className: "file-selected", href: item.link, target: "_blank" },
					item.name
				)
			)
		);
	}
});